import React from 'react'
import ReactDOM from 'react-dom'
import {
  Router,
  LocationProvider,
  Location,
  createMemorySource,
  createHistory,
  navigate,
  Link
} from '@reach/router'
import posed, { PoseGroup } from 'react-pose'

let source = createMemorySource('/')
let history = createHistory(source)

let transition = {
  type: 'spring',
  stiffness: 200,
  damping: 20
}

let RouteContainer = posed.div({
  enter: { x: '0%', transition },
  preEnter: { x: '100%', transition },
  exit: { x: '-100%', transition }
})

let MenuLink = ({ to, children }) => (
  <Link
    to={to}
    style={{
      textDecoration: 'none',
      display: 'flex',
      alignItems: 'center',
      height: 48,
      borderRadius: 4,
      color: '#453f56',
      padding: '0 32px',
      marginBottom: 16,
      transition: 'background 0.3s'
    }}
    className="menu-link"
  >
    {children}
  </Link>
)

let Home = () => (
  <div style={{ padding: 32 }}>
    <MenuLink to="/page-options">
      <svg
        width={20}
        height={20}
        fill="currentColor"
        style={{ marginRight: 16 }}
      >
        <path d="M15.95 10.78c.03-.25.05-.51.05-.78s-.02-.53-.06-.78l1.69-1.32c.15-.12.19-.34.1-.51l-1.6-2.77c-.1-.18-.31-.24-.49-.18l-1.99.8c-.42-.32-.86-.58-1.35-.78L12 2.34c-.03-.2-.2-.34-.4-.34H8.4c-.2 0-.36.14-.39.34l-.3 2.12c-.49.2-.94.47-1.35.78l-1.99-.8c-.18-.07-.39 0-.49.18l-1.6 2.77c-.1.18-.06.39.1.51l1.69 1.32c-.04.25-.07.52-.07.78s.02.53.06.78L2.37 12.1c-.15.12-.19.34-.1.51l1.6 2.77c.1.18.31.24.49.18l1.99-.8c.42.32.86.58 1.35.78l.3 2.12c.04.2.2.34.4.34h3.2c.2 0 .37-.14.39-.34l.3-2.12c.49-.2.94-.47 1.35-.78l1.99.8c.18.07.39 0 .49-.18l1.6-2.77c.1-.18.06-.39-.1-.51l-1.67-1.32zM10 13c-1.65 0-3-1.35-3-3s1.35-3 3-3 3 1.35 3 3-1.35 3-3 3z" />
      </svg>
      Page Options
    </MenuLink>
    <MenuLink to="/blog-posts">
      <svg
        width={20}
        height={20}
        fill="currentColor"
        style={{ marginRight: 16 }}
        viewBox="0 0 24 24"
      >
        <path d="M3 17.25V21h3.75L17.81 9.94l-3.75-3.75L3 17.25zM20.71 7.04c.39-.39.39-1.02 0-1.41l-2.34-2.34c-.39-.39-1.02-.39-1.41 0l-1.83 1.83 3.75 3.75 1.83-1.83z" />
      </svg>
      Blog Posts
    </MenuLink>
  </div>
)
let BackLink = ({ to }) => (
  <Link
    to={to}
    style={{
      display: 'inline-flex',
      alignItems: 'center',
      textDecoration: 'none',
      color: '#453f56'
    }}
    data-back
  >
    <svg width={24} height={24} fill="currentColor" style={{ marginRight: 8 }}>
      <path d="M21 11H6.83l3.58-3.59L9 6l-6 6 6 6 1.41-1.41L6.83 13H21z" />
    </svg>
    Back
  </Link>
)
let PageOptions = () => (
  <div style={{ padding: 32 }}>
    <BackLink to="/" />
    <h2>Page Options</h2>
  </div>
)
let BlogPosts = () => (
  <div style={{ padding: 32 }}>
    <BackLink to="/" />
    <h2>Blog Posts</h2>
  </div>
)
let EditableOptions = ({ options }) => (
  <div style={{ padding: 32 }}>
    <ul>
      {options.map(option => (
        <li>{option}</li>
      ))}
    </ul>
  </div>
)

class App extends React.Component {
  state = {
    preEnterPose: 'preEnter',
    exitPose: 'exit',
    editableOptions: []
  }
  handleClick = e => {
    let a = e.target.closest('a')
    if (!a) return
    if (a.getAttribute('data-back')) {
      this.setState({ preEnterPose: 'exit', exitPose: 'preEnter' })
    } else {
      this.setState({ preEnterPose: 'preEnter', exitPose: 'exit' })
    }
  }
  showEditableOptions = editableOptions => {
    this.setState(
      { preEnterPose: 'preEnter', exitPose: 'exit', editableOptions },
      () => {
        history.navigate('/editable-options')
      }
    )
  }
  componentDidMount() {
    window.showEditableOptions = this.showEditableOptions
  }
  render() {
    return (
      <div
        style={{ display: 'flex', height: '100vh' }}
        onClickCapture={this.handleClick}
      >
        <div
          style={{
            width: 304,
            flex: 'none',
            background: '#f3f3f7'
          }}
        >
          <LocationProvider history={history}>
            <Location>
              {({ location }) => (
                <PoseGroup
                  preEnterPose={this.state.preEnterPose}
                  exitPose={this.state.exitPose}
                >
                  <RouteContainer key={location.pathname}>
                    <Router location={location}>
                      <Home path="/" />
                      <PageOptions path="/page-options" />
                      <BlogPosts path="/blog-posts" />
                      <EditableOptions
                        path="/editable-options"
                        options={this.state.editableOptions}
                      />
                    </Router>
                  </RouteContainer>
                </PoseGroup>
              )}
            </Location>
          </LocationProvider>
        </div>
        <iframe
          src="/nocache"
          style={{ position: 'relative', border: 0, width: '100%' }}
        />
      </div>
    )
  }
}

ReactDOM.render(<App />, document.getElementById('app'))
